.about.markdown-text {
  background-color: #fff;
  padding: 0 20px 20px; }
  .about.markdown-text h2, .about.markdown-text h3 {
    margin: 30px 0 15px;
    border-bottom: 1px solid #eee;
    font-size: 26px; }
    .about.markdown-text h2:first-child, .about.markdown-text h3:first-child {
      margin-top: 0;
      padding-top: 30px; }
  .about.markdown-text h3 {
    font-size: 24px; }
  .about.markdown-text p {
    font-size: 15px;
    line-height: 1.7em;
    overflow: auto;
    margin: 1em 0; }
  .about.markdown-text a {
    color: #08c; }
